export const QuanHeHaiNgoi = () => {
    return (
        <>
            <Div id="1">
                <RightH2>Quan h hai ngi</RightH2>
                <RightH3 id="1_1">1. nh ngha</RightH3>
                <RightP>Cho hai tp A, B. Ta gi tp R l mt quan h hai ngi t A n B <br /> nu R &sube; A x B.</RightP>
                <RightP>Nu (a, b) &isin; R th ta ni a c quan h R vi b v k hiu a R b; ngc li nu (a, b) &notin; R th ta k hiu  a <span style={{ borderTop: "1px solid" }}>R</span> b.</RightP>
                <RightP>Khi A = B, ta gi R l mt quan h hai ngi trn A.</RightP>
                <RightP><span className="font-weight-bold">V d:</span></RightP>
                <RightP>Cho A = {'{'}1, 2, 3, 4{'}'}, R l mt quan h (hai ngi) trn A v <br /> R = {'{'}(a, b) &isin; A | a l c ca b {'}'}.</RightP>
                <RightP>Khi : </RightP>
                <RightP>R = {'{'}(1, 1), (1, 2), (1, 3), (1, 4), (2, 2), (2, 4), (3, 3), (4,4){'}'}</RightP>
                <Image style={{ marginLeft: "80px" }} src={require('../../images/quanhe/1_1.png').default} />
                <RightH3 id="1_2">2. Cc tnh cht</RightH3>
                <RightH4 id="1_2_a">a. Tnh phn x</RightH4>
                <RightP>Gi s R l mt quan h hai ngi trn tp A.<br />Ta ni quan h R c tnh phn x nu v ch nu a R a ,  &forall; a &isin; A.</RightP>
                <RightP><span className="font-weight-bold">V d:</span></RightP>
                <RightP>Trn tp A = {'{'}1, 2, 3, 4{'}'}, <br />
                    quan h R1 = {'{'}(1,1), (1,2), (2,1), (2, 2), (3, 4), (4, 1), (4, 4){'}'} khng phn x v (3, 3) &notin; R1 <br />
                    R2 = {'{'}(1,1), (1,2), (1,4), (2, 2), (3, 3), (4, 1), (4, 4){'}'} phn x v (1,1), (2, 2), (3, 3), (4, 4) &isin; R2 </RightP>
                <RightP>
                    - Quan h    trn Z phn x v a    a, &forall; a &isin; Z. <br />
                    - Quan h &gt; trn Z khng phn x v 1 khng ln hn 1. <br />
                    - Quan h  |  (c s) trn Z+ l phn x v mi s nguyn dng a l c ca chnh n.</RightP>
                <RightH4 id="1_2_b">b. Tnh i xng, phn xng</RightH4>
                <RightP>Gi s R l mt quan h hai ngi trn tp A. <br />
                    - Ta ni quan h R c tnh i xng nu v ch nu a R b &rArr; b R a , &forall; a, b &isin; A. <br />
                    - Ta ni quan h R c tnh phn xng nu v ch nu (a R b &and;  b R a) &rArr; a = b ,  &forall; a, b &isin; A.</RightP>
                <RightP><span className="font-weight-bold">V d:</span></RightP>
                <RightP>
                    - Quan h R1 = {'{'}(1,1), (1,2), (2,1){'}'} trn tp A = {'{'}1, 2, 3, 4{'}'} l i xng.<br />
                    - Quan h    trn Z khng i xng, tuy nhin n phn xng v (a   b) &and; (b    a) &rArr; (a = b).<br />
                    - Quan h  |  ( c s ) trn Z+  khng i xng, tuy nhin n c tnh phn xng v (a | b) &and; (b | a) &rArr; (a = b).</RightP>
                <RightH4 id="1_2_c">c. Tnh bc cu ( truyn )</RightH4>
                <RightP>
                    Gi s R l mt quan h hai ngi trn tp A.<br />
                    - Ta ni quan h R c tnh bc cu (truyn) nu v ch nu <br />
                    (a R b &and; b R c) &rArr; a R c , &forall; a,b,c &isin; A.
                </RightP>
                <RightP><span className="font-weight-bold">V d:</span></RightP>
                <RightP>
                    Quan h R = {'{'}(1,1), (1,2), (2,1), (2, 2), (1, 3), (2, 3){'}'} trn tp A = {'{'}1, 2, 3, 4{'}'} c tnh bc cu.<br />
                    - Quan h  v | trn Z c tnh bc cu v <br />
                    (a  b) &and; (b  c) &rArr; (a  c)<br />(a | b) &and; (b | c) &rArr; (a | c)</RightP>
                <RightH4 id="1_2_d">d. Biu din quan h</RightH4>
                <RightP>
                    Cho R l quan h t A = {'{'}a1, a2, , a<sub>m</sub>{'}'} n B = {'{'}b1, b2, , bn{'}'}. Ma trn biu din ca R l ma trn M<sub>R</sub> = [m<sub>ij</sub>] <sub>mxn</sub> xc nh bi:
                </RightP>
                <Image style={{ marginLeft: "80px" }} src={require('../../images/quanhe/1_2_d.png').default} />
                <RightP><span className="font-weight-bold">V d:</span></RightP>
                <RightP>
                    Cho R l quan h t A = {'{'}1, 2, 3, 4{'}'} n B = {'{'}u, v, w{'}'},<br /> R = {'{'}(1, u), (1, v), (2, w), (3, w), (4, u){'}'}.<br /> Khi  R c th biu din nh sau:<br />
                    <Image style={{ marginLeft: "80px" }} src={require('../../images/quanhe/1_2_d1.png').default} />
                </RightP>
                <RightP>y l ma trn cp 43 biu din cho quan h R</RightP>
                <RightP></RightP>
                <RightP>Cho R l quan h t A = {'{'}1, 2, 3{'}'} n B = {'{'}1, 2{'}'}:<br /> a R b &hArr; a &gt; b. Khi  ma trn biu din ca R l: <br /></RightP>
                <Image style={{ marginLeft: "80px" }} src={require('../../images/quanhe/1_2_d2.png').default} />
                <RightP>Cho R l quan h trn tp A, khi  MR l ma trn vung.</RightP>

                <RightP>+ R l phn x nu tt c cc phn t trn ng cho ca M<sub>R</sub> u bng 1:<br /></RightP>
                <Image style={{ marginLeft: "80px" }} src={require('../../images/quanhe/1_2_d3.png').default} />

                <RightP>+ R l i xng nu M<sub>R</sub> l i xng m<sub>ij</sub> = m<sub>ji</sub>, &forall; i, j.<br /></RightP>
                <Image style={{ marginLeft: "80px" }} src={require('../../images/quanhe/1_2_d4.png').default} />

                <RightP>+ R l phn xng nu M<sub>R</sub> tha: <br />
                    m<sub>ij</sub> = 0 hoc  m<sub>ji</sub> = 0 nu i &ne; j.<br /></RightP>
                <Image style={{ marginLeft: "80px" }} src={require('../../images/quanhe/1_2_d5.png').default} />
            </Div>
        </>
    )
}
