// 43
export const HamBoole = () => {
    return (
        <>
            <RightH2 id="4">Hm Boole</RightH2>
            <RightH3 id="4_1">1. nh ngha</RightH3>
            <RightP>
                - nh x f: B<sup>n</sup> &rarr;B gi l mt hm Boole n bin.<br />
                - Hm ng nht bng 1 k hiu l 1, hm ng nht bng 0 k hiu l 0. Tp tt c cc hm Boole n  bin k hiu l F<sub>n</sub>.</RightP>
            <RightP>Cho f v g l hai hm Boole n bin. Chng ta c cc nh ngha nh sau:<br />
                1. (f &and; g)(x<sub>1</sub>, , x<sub>n</sub>) = f(x<sub>1</sub>, , x<sub>n</sub>) &and; g(x<sub>1</sub>, , x<sub>n</sub>)<br />
                2. (f &or; g)(x<sub>1</sub>, , x<sub>n</sub>) = f(x<sub>1</sub>, , x<sub>n</sub>) &or; g(x<sub>1</sub>, , x<sub>n</sub>)<br />
                3. f<sub>/</sub>(x<sub>1</sub>, , x<sub>n</sub>) = (f(x<sub>1</sub>, , x<sub>n</sub>))<br />
                vi mi x1, , xn.</RightP>
            <RightP>
                - Ta c F<sub>n</sub> cng cc php ton ny lp thnh mt i s Boole.<br />
                - Ngoi ra cn c:<br />
                f &le; g &hArr; f &or; g = g &hArr; f &and; g = f<br />
                trong  f &le; g nu <br />
                f(x<sub>1</sub>, , x<sub>n</sub>) &le; g(x<sub>1</sub>, , x<sub>n</sub>)<br />
                Cch thng thng nht  xc nh mt hm Boole l dng bng gi tr.</RightP>
            <RightP><RightWordItalic>Hm Boole hai bin</RightWordItalic></RightP>
            <Image style={{ marginLeft: "80px" }} src={require('../../images/boole/4_1.png').default} />
            <RightP><RightWordBold>V d:</RightWordBold></RightP>
            <RightP>
                - Xt kt qu f trong vic thng qua mt quyt nh da vo 3 phiu bu x, y, z: <br />
                1.	Mi phiu ch ly mt trong hai gi tr:<br />
                1 (tn thnh) hoc 0 (bc b).<br />
                2.	Kt qu f: <br />
                - l 1 (thng qua quyt nh) nu c a s phiu tn thnh.<br />
                - l 0 (khng thng qua quyt nh) nu a s phiu bc b.<br />
                - Khi  f l hm Bool theo 3 bin x,y,x c bng chn tr nh sau:</RightP>
            <Image style={{ marginLeft: "80px" }} src={require('../../images/boole/4_1_2.png').default} />
            <RightP>
                - Chng ta cng c th xc nh hm Boole bng mt biu thc Boole.  l mt biu thc gm cc bin Boole v cc php ton &and; (hi), &or; (tuyn), / (php ly b).<br />
                <RightWordItalic>Mi biu thc Boole cng c xem nh mt hm Boole.</RightWordItalic></RightP>

            <RightH3 id="4_2">2. Tch s cp, cc php ton trn hm boole</RightH3>
            <RightP>
                - Bin x gi l bin Boole nu x ch nhn mt trong hai gi tr 0/1.<br />
                - Gi s x l mt bin Boole. Khi  k hiu x<sup>1</sup> = x, x<sup>0</sup> = &#9488;x.<br />
                <RightWordBold>Cc php ton trn hm boole</RightWordBold></RightP>
            {/* Php cng boole */}
            <RightP><RightWordBold> Phep cng Boole &or;:</RightWordBold></RightP>
            <RightP>
                Vi f, g &isin; F<sub>n</sub>, ta inh nghia tng Boole cua f va g:<br />
                f &or; g = f + g - fg<br />
                &forall; x = (x<sub>1</sub>, x<sub>2</sub>,, x<sub>n</sub> ) &isin; B<sub>n</sub>,<br />
                &rarr; (f &or; g)(x) = f(x) + g(x)  f(x)g(x)</RightP>
            {/* Php nhn boole */}
            <RightP><RightWordBold> Phep cng Boole &and;:</RightWordBold></RightP>
            <RightP>
                Vi f, g &isin; F<sub>n</sub>, ta inh nghia tng Boole cua f va g:<br />
                f &and; g = fg<br />
                &forall; x = (x<sub>1</sub>, x<sub>2</sub>,, x<sub>n</sub> ) &isin; B<sub>n</sub>,<br />
                &rarr; (f &and; g)(x) = f(x)g(x)</RightP>
            {/* Php ly phn b */}
            <RightP><RightWordBold> Phep ly phn b:</RightWordBold></RightP>
            <RightP><span style={{ borderTop: "1px solid #000" }}>f</span> = 1 - f</RightP>
            <RightH3 id="4_3">3. Biu thc Boole</RightH3>
            <RightP>
                - L mt biu thc c to bi cc bin v cc php ton Boole.<br />
                VD: E= (x &and; y &and; z) &or; (z &and; <span style={{ borderTop: "1px solid #000" }}>y</span>)<br />
                -  d oc hn, ngi ta co th vit:<br />
                E = xyz + z<span style={{ borderTop: "1px solid #000" }}>y</span></RightP>
            <RightH3 id="4_4">4. Dng ni ri chnh tc</RightH3>
            <RightP>Xet tp hp cac ham Boole n bin F<sub>n</sub> theo n bin x<sub>1</sub>, x<sub>2</sub>, ,x<sub>n</sub>.</RightP>
            <RightP>- Mi ham Boole x<sub>i</sub> hay <span style={{ borderTop: "1px solid #000" }}>x<sub>i</sub></span> c goi la mt t n.<br />
                - n thc la tich khac khng cua mt s hu han t n.<br />
                - T ti tiu (n thc ti tiu) la tich khac khng cua ung n t n.<br />
                - Cng thc a thc la cng thc biu din ham Boole thanh tng cua cac n thc.<br />
                - Dang ni ri chinh tc la cng thc biu din ham Boole thanh tng cua cac t ti tiu.</RightP>
            <RightP><RightWordBold>V d:</RightWordBold></RightP>
            <RightP>Xt hm boole, vi 3 bin: x, y, z<br />
                x, y, z, <RightWordNot>x</RightWordNot>, <RightWordNot>y</RightWordNot>, <RightWordNot>z</RightWordNot> la cac t n.<br />
                xy, yz la n thc<br />
                xy<RightWordNot>z</RightWordNot> la t ti tiu<br />
                E = xy + yz la mt cng thc a thc<br />
                Va F = xyz + <RightWordNot>x</RightWordNot> <RightWordNot>y</RightWordNot> <RightWordNot>z</RightWordNot> la mt dang ni ri chinh tc</RightP>
            <RightP>
                Cho f &isin; F<sub>n</sub>, f c th vit di dng sau:<br />
                f = u<sub>1</sub> &or; u<sub>2</sub> &or; u<sub>3</sub> &or; ... &or;  u<sub>i</sub><br />
                Vi u<sub>i</sub>  l cc n thc ti tiu bc n (i = 1, , n).<br />
                c gi l dng ni ri chnh tc ca f.</RightP>
            <RightP><RightWordBold>V d:</RightWordBold></RightP>
            <RightP>
                - Trong F<sub>4</sub> c dng biu din sau y:<br />
                f(x,y,z,t) = x<RightWordNot>y</RightWordNot><RightWordNot>z</RightWordNot>t   <RightWordNot>x</RightWordNot>yzt  xy<RightWordNot>z</RightWordNot><RightWordNot>t</RightWordNot><br />
                 f c dng ni ri chnh tc ca hm Bool.</RightP>
            <RightP><RightWordBold>C 2 cch  xc nh dng ni ri chnh tc mt hm Bool:</RightWordBold></RightP>
            {/* Cach 1 */}
            <RightP><RightWordBold>Cch 1:</RightWordBold>B sung t n cn thiu vo cc n thc.</RightP>
            <RightP>
                - Bc 1: Khai trin hm Bool thnh tng ca cc n thc.<br />
                - Bc 2: Vi mi n thc thu c  bc 1, ta nhn n thc  vi cc tng dng vi xi l nhng t n b thiu trong n thc .<br />
                - Bc 3: Tip tc khai trin hm thu c  bc 2 v loi b nhng n thc b trng. Cng thc a thc thu c chnh l dng ni ri chnh tc ca hm Bool ban u.</RightP>
            <RightP><RightWordBold>V d: </RightWordBold>Trong F<sub>3</sub> tm dng ni ri chnh tc</RightP>
            <RightP>
                f(x,y,z) = <RightWordNot>x</RightWordNot> &or; <RightWordNot>y</RightWordNot>z &or; xy<RightWordNot>z</RightWordNot><br />
                = <RightWordNot>x</RightWordNot>(y &or; <RightWordNot>y</RightWordNot>).(z &or; <RightWordNot>z</RightWordNot>) &or; (<RightWordNot>x</RightWordNot> &or; x)<RightWordNot>y</RightWordNot>z &or; xy<RightWordNot>z</RightWordNot><br />
                = <RightWordNot>x</RightWordNot>yz &or; <RightWordNot>x</RightWordNot>y<RightWordNot>z</RightWordNot> &or; <RightWordNot>x</RightWordNot><RightWordNot>y</RightWordNot>z &or; <RightWordNot>x</RightWordNot><RightWordNot>y</RightWordNot><RightWordNot>z</RightWordNot> &or; <RightWordNot>x</RightWordNot><RightWordNot>y</RightWordNot>z &or; x<RightWordNot>y</RightWordNot>z &or; xy<RightWordNot>z</RightWordNot><br />
                &rArr; f c dng ni ri chnh tc ca hm Bool.</RightP>
            {/* Cach 2 */}
            <RightP><RightWordBold>Cch 2:</RightWordBold>Dng bng chn tr.   n cc vector boole trong bng chn tr m ti  f = 1.</RightP>
            <RightP>Ti  Vector bool th n l u<sub>1</sub>, u<sub>2</sub>, , u<sub>n</sub> v f(u<sub>1</sub>, u<sub>2</sub>, , u<sub>n</sub>) = 1</RightP>
            <RightP><RightWordBold>V d:</RightWordBold>
                Cho f(x,y) = x &or; <RightWordNot>y</RightWordNot>. <br />
                Tm biu thc dng ni ri chnh tc ca f.</RightP>
            <Div className="d-flex justify-content-between">
                <RightP>Lp bng chn tr ca f:</RightP>
                <Image src={require('../../images/boole/4_4.png').default} />
            </Div>
            <RightP>Cc th hin lm cho f = 1 l 00, 10, 11.<br />
                &rarr; Lp c cc t ti tiu tng ng. <br />
                Vy dng ni ri chnh tc ca f l f(x,y) = <RightWordNot>x</RightWordNot><RightWordNot>y</RightWordNot> &or; x<RightWordNot>y</RightWordNot> &or; xy.</RightP>
        </>
    )
}
// 44
export const TichSoCap = () => {
    return (
        <>
            <RightH3 id="4_2">Tch s cp, cc php ton trn hm boole</RightH3>
            <RightP>
                - Bin x gi l bin Boole nu x ch nhn mt trong hai gi tr 0/1.<br />
                - Gi s x l mt bin Boole. Khi  k hiu x<sup>1</sup> = x, x<sup>0</sup> = &#9488;x.<br />
                <RightWordBold>Cc php ton trn hm boole</RightWordBold></RightP>
            {/* Php cng boole */}
            <RightP><RightWordBold> Phep cng Boole &or;:</RightWordBold></RightP>
            <RightP>
                Vi f, g &isin; F<sub>n</sub>, ta inh nghia tng Boole cua f va g:<br />
                f &or; g = f + g - fg<br />
                &forall; x = (x<sub>1</sub>, x<sub>2</sub>,, x<sub>n</sub> ) &isin; B<sub>n</sub>,<br />
                &rarr; (f &or; g)(x) = f(x) + g(x)  f(x)g(x)</RightP>
            {/* Php nhn boole */}
            <RightP><RightWordBold> Phep cng Boole &and;:</RightWordBold></RightP>
            <RightP>
                Vi f, g &isin; F<sub>n</sub>, ta inh nghia tng Boole cua f va g:<br />
                f &and; g = fg<br />
                &forall; x = (x<sub>1</sub>, x<sub>2</sub>,, x<sub>n</sub> ) &isin; B<sub>n</sub>,<br />
                &rarr; (f &and; g)(x) = f(x)g(x)</RightP>
            {/* Php ly phn b */}
            <RightP><RightWordBold> Phep ly phn b:</RightWordBold></RightP>
            <RightP><span style={{ borderTop: "1px solid #000" }}>f</span> = 1 - f</RightP>
        </>
    )
}

