import { React, useState } from 'react'
import { Nav, NavbarContainer, NavbarItem, NavbarLink, NavbarLogo, NavbarMenu, NavbarSearch } from './NavbarElements'
import { animateScroll as scroll } from "react-scroll";
import {FaSearch} from 'react-icons/fa'

const NavbarSection = () => {
    const [navbar, setNavbar] = useState(false);
    const changeBackground = () => {
        if (window.scrollY >= 80) {
            setNavbar(true);
        } else {
            setNavbar(false);
        }
    }
    window.addEventListener('scroll', changeBackground);

    const scrollToTop = () => {
        scroll.scrollToTop();
    }
    return (
        <>
            <Nav className={navbar ? 'navbar changeBg' : 'navbar'}>
                <NavbarContainer>
                    <NavbarLogo to="/toanroirac">TonRR</NavbarLogo>
                    <NavbarMenu>
                        <NavbarItem>
                            <NavbarLink to="" onClick={scrollToTop}>Trang ch</NavbarLink>
                        </NavbarItem>
                        <NavbarItem>
                            <NavbarLink activeClass="active"
                                to="logic"
                                spy={true}
                                smooth={true}
                                offset={-70}
                                duration={500}>Ni dung</NavbarLink>
                        </NavbarItem>
                        <NavbarItem>
                            <NavbarSearch to="/toanroirac/baitap">Bi tp</NavbarSearch>
                        </NavbarItem>
                        <NavbarItem>
                            <NavbarLink activeClass="active"
                                to="copyright"
                                spy={true}
                                smooth={true}
                                offset={-70}
                                duration={500}>Bn quyn</NavbarLink>
                        </NavbarItem>
                        <NavbarItem>
                            <NavbarSearch to="/toanroirac/search"><FaSearch/></NavbarSearch>
                        </NavbarItem>
                    </NavbarMenu>
                </NavbarContainer>
            </Nav>
        </>
    )
}

export default NavbarSection
